@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap");

:root {
  /* Color Variables */
  --primary-gradient: linear-gradient(135deg, #7367f0 0%, #00cfe8 100%);
  --accent-blue: #00cfe8;
  --accent-purple: #7367f0;
  --background-white: #ffffff;
  --light-gray-bg: #f8f9fa;
  --text-gray: #495057;
  --subdued-gray: #6c757d;
  --dark-text: #212529;

  /* Typography */
  --font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;

  /* Spacing */
  --spacing-xs: 4px;
  --spacing-sm: 8px;
  --spacing-md: 16px;
  --spacing-lg: 24px;
  --spacing-xl: 32px;
  --spacing-2xl: 48px;

  /* Border Radius */
  --radius-sm: 8px;
  --radius-md: 16px;
  --radius-lg: 24px;
  --radius-pill: 50px;

  /* Shadows */
  --shadow-soft: 0 4px 20px rgba(0, 0, 0, 0.05);
  --shadow-medium: 0 8px 25px rgba(0, 0, 0, 0.1);
  --shadow-strong: 0 12px 40px rgba(0, 0, 0, 0.15);
}

body {
  margin: 0;
  font-family: var(--font-family);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--background-white);
  color: var(--text-gray);
  line-height: 1.6;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
/* Layout Utilities */
.grid-container {
  display: grid;
  grid-template-columns: 66.67% 33.33%;
  gap: 0;
  height: 100vh;
  background: var(--background-white);
}

.grid-item {
  padding: 0;
  text-align: left;
}

.item1 {
  background-color: var(--background-white);
  height: 100vh;
  overflow-y: hidden;
  position: relative;
}

.item2 {
  background: var(--light-gray-bg);
  overflow-y: auto;
  position: relative;
}

/* Typography Classes */
.headline {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--dark-text);
  margin-bottom: var(--spacing-md);
  letter-spacing: -0.02em;
  line-height: 1.2;
}

.subheadline {
  font-size: 1.25rem;
  font-weight: 400;
  color: var(--subdued-gray);
  margin-bottom: var(--spacing-lg);
  line-height: 1.5;
}

.body-text {
  font-size: 1rem;
  color: var(--text-gray);
  line-height: 1.6;
  margin-bottom: var(--spacing-md);
}

/* Button Styles */
.btn-primary {
  background: var(--primary-gradient);
  color: var(--background-white);
  border: none;
  border-radius: var(--radius-pill);
  font-weight: 600;
  padding: 12px 24px;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: var(--shadow-soft);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-medium);
}

.btn-secondary {
  background: var(--background-white);
  color: var(--accent-purple);
  border: 2px solid var(--accent-purple);
  border-radius: var(--radius-pill);
  font-weight: 600;
  padding: 10px 22px;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-secondary:hover {
  background: var(--accent-purple);
  color: var(--background-white);
  transform: translateY(-2px);
  box-shadow: var(--shadow-soft);
}

/* Card Styles */
.card {
  background: var(--background-white);
  border-radius: var(--radius-md);
  box-shadow: var(--shadow-soft);
  padding: var(--spacing-lg);
  margin-bottom: var(--spacing-lg);
  transition: all 0.3s ease;
  border: 4px solid var(--background-white);
}

.card:hover {
  box-shadow: var(--shadow-medium);
  transform: translateY(-2px);
}

.card-active {
  background: var(--background-white);
  border-radius: var(--radius-md);
  box-shadow: var(--shadow-medium);
  padding: var(--spacing-lg);
  margin-bottom: var(--spacing-lg);
  border: 4px solid var(--accent-blue);
  transition: all 0.3s ease;
}

/* Section Styles */
.section-header {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--dark-text);
  margin-bottom: var(--spacing-md);
  letter-spacing: -0.02em;
  line-height: 1.2;
}

/* Gradient Backgrounds */
.gradient-bg {
  background: var(--primary-gradient);
}

.gradient-wave {
  background: linear-gradient(
    135deg,
    rgba(115, 103, 240, 0.1) 0%,
    rgba(0, 207, 232, 0.1) 100%
  );
}

/* Scrollbar Styling */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--light-gray-bg);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: var(--accent-purple);
  border-radius: 4px;
  opacity: 0.6;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--accent-blue);
  opacity: 0.8;
}

/* Focus States */
button:focus,
input:focus,
textarea:focus {
  outline: 2px solid var(--accent-blue);
  outline-offset: 2px;
}

/* Animation Utilities */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-fade-in-up {
  animation: fadeInUp 0.6s ease-out;
}

/* Chart-specific styles */
.chart-container {
  background: var(--light-gray-bg);
  border-radius: var(--radius-sm);
  padding: var(--spacing-sm);
  border: 1px solid rgba(115, 103, 240, 0.1);
}

.chart-title {
  color: var(--dark-text);
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: var(--spacing-md);
  text-align: center;
  letter-spacing: -0.01em;
}

.chart-canvas {
  border: 1px solid rgba(115, 103, 240, 0.2);
  border-radius: var(--radius-sm);
  background: var(--background-white);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  display: block;
}

/* Responsive Design */
@media (max-width: 768px) {
  .headline {
    font-size: 2rem;
  }

  .section-header {
    font-size: 2rem;
  }

  .card {
    padding: var(--spacing-md);
  }

  .btn-primary,
  .btn-secondary {
    padding: 10px 16px;
    font-size: 0.8rem;
  }

  .chart-title {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .headline {
    font-size: 1.75rem;
  }

  .section-header {
    font-size: 1.75rem;
  }

  .card {
    padding: var(--spacing-sm);
    margin-bottom: var(--spacing-md);
  }

  .chart-title {
    font-size: 0.875rem;
  }
}
