.App {
  text-align: left;
  font-family: var(--font-family);
  background: var(--background-white);
  min-height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background: var(--primary-gradient);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: var(--background-white);
}

.App-link {
  color: var(--accent-blue);
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
}

.App-link:hover {
  color: var(--background-white);
  text-decoration: underline;
}

/* App footer */
.app-footer {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--light-gray-bg);
  color: var(--subdued-gray);
  font-size: 12px;
  line-height: 1.4;
  z-index: 100;
  border-top: 1px solid rgba(115, 103, 240, 0.1);
}

.app-footer .footer-accent {
  height: 2px;
  background: var(--primary-gradient);
}

.app-footer .footer-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  gap: var(--spacing-sm);
  padding: var(--spacing-sm) var(--spacing-md);
}

.app-footer .footer-left {
  display: flex;
  align-items: center;
  gap: 0.3em;
  flex-wrap: wrap;
  font-weight: 500;
}

.app-footer .footer-right {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 0;
  text-align: right;
  font-size: 11px;
}

.app-footer .footer-left,
.app-footer .footer-right {
  min-width: 0;
}

.app-footer .footer-left a {
  white-space: nowrap;
  color: var(--accent-purple);
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s ease;
}

.app-footer .footer-left a:hover,
.app-footer .footer-left a:focus {
  color: var(--accent-blue);
  text-decoration: underline;
}

@media (max-width: 640px) {
  .app-footer .footer-right {
    flex-basis: 100%;
    margin-left: 0;
    justify-content: flex-end;
  }
}

.app-footer a {
  color: var(--accent-purple);
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
}

.app-footer a:hover,
.app-footer a:focus {
  color: var(--accent-blue);
  text-decoration: underline;
}

@keyframes pop-in {
  0% {
    height: 1px;
    width: 1px;
    transform: translate(-0.5, -0.5);
    opacity: 0;
    left: -0.5px;
    top: -0.5px;
  }
  50% {
    height: 50px;
    width: 50px;
    transform: translate(-25, -25);
    opacity: 0.5;
    left: -25px;
    top: -25px;
  }
  100% {
    height: 10px;
    width: 10px;
    transform: translate(-2.5, -2.5);
    opacity: 1;
    left: -5px;
    top: -5px;
  }
}

.single-point {
  animation: pop-in 5s ease-out;
  left: -5px;
  top: -5px;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.single-point-annotation {
  opacity: 0;
  animation-name: fadeIn;
  animation-duration: 2s;
  animation-fill-mode: forwards;
}

path {
  stroke-linejoin: round !important;
}
